.travelopia-table {
	max-width: 1000px !important;
	overflow: scroll;

	table {
		width: 100%;
		border-collapse: collapse;
		border-spacing: 0;
	}

	&__column {

		&--sticky {
			position: sticky !important;
			left: 0;
			z-index: 1;
		}

		&.alignleft {
			text-align: left;
		}

		&.aligncenter {
			text-align: center;
		}

		&.alignright {
			text-align: right;
		}
	}

	&__row-container {

		&--sticky {

			tr th {
				position: sticky !important;
				top: 0;
				z-index: 1;
			}
		}
	}

	td,
	th {
		border: 1px solid #ddd;
		padding: 8px;
		height: 50px;
		min-width: 90px;
	}

	span[data-rich-text-placeholder] {
		visibility: hidden;
	}

	span {
		white-space: nowrap !important;
	}
}
